import {HTTP} from '@/common/http-common'

export default {
  state: {
    user: null
  },

  getters: {
    user (state) {
      if (state.user == null) {
        state.user = JSON.parse(localStorage.getItem('user'))
      }
      return state.user
    },

    isLoggedIn (state, getters) {
      return getters.user != null
    },

    token (state, getters) {
      return getters.user ? getters.user.token : null
    },

    helloToken (state) {
      if (state.helloToken == null) {
        state.helloToken = JSON.parse(localStorage.getItem('hello')) ? JSON.parse(localStorage.getItem('hello')).google.access_token : null
      }
      return state.helloToken
    }
  },

  mutations: {
    user (state, payload) {
      state.user = payload
      localStorage.setItem('user', JSON.stringify(state.user))
    },
    logout (state) {
      localStorage.removeItem('user')
      localStorage.removeItem('hello')
    }
  },

  actions: {
    authenticate ({commit, getters}, payload) {
      console.log('in authenticate', payload)
      var imageUrl = payload.image.url
      var t = getters.helloToken
      var reqObj = {
        id: payload.id,
        displayName: payload.displayName,
        name: payload.name,
        image: encodeURI(imageUrl),
        email: payload.email,
        gender: payload.gender,
        url: encodeURI(payload.url),
        thirdPartyAccessToken: t
      }

      commit('isLoading', true)
      HTTP.post('token', reqObj).then(response => {
        commit('user', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    logout ({commit}) {
      commit('logout', null)
    }
  }
}



